body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  min-height: 100vh;
  max-height: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.react-container {
  width: 95%;
}

.input-task-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 500px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.task-input {
  width: 90%;
  border: none;
  border-bottom: 2px solid #f9a400;
  padding-top: 1rem;
  padding-left: 0.6rem;
  box-shadow: 5px 5px 0 rgba(0,0,0,0.1);
  margin-bottom: 0.5rem;
}

.task-input--error {
  border: 3px solid #ed0000;
}

.btn.new-task-btn {
  border: none;
  background-color: #ebcd65;
  border-radius: 50%;
  font-size: 1.8rem;
  font-weight: bold;
  width: 2rem;
  height: 2rem;
  padding: 0;
  margin: 1rem 0;
}

.btn.new-task-btn:hover {
  background-color: #ffca2a;
}

body {
  background-color: #dbe79f;
}

.wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  width: 98%;
  color: #fff;
  font-size: 1.3em;
}

.task {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background: white;
  border-radius: 5px;
  box-shadow: 5px 5px 0 rgba(0,0,0,0.1);
  padding: 1rem 2rem 1rem 5rem;
  background: -webkit-gradient(linear, 0 0, 0 100%, from(#d9eaf3), color-stop(4%, #fff)) 0 4px;
  background-size: 100% 3rem;
  line-height: 3rem;
  margin: 0.5rem 0;
  padding: 10px 65px 10px 10px;
  min-width: 600px;
  max-width: 70%;
  min-height: 75px;
  height: auto;
  position: relative;
}

.task:before {
  content: '';
  position: absolute;
  width: 4px;
  top: 0;
  right: 50px;
  bottom: 0;
  border: 1px solid;
  border-color: transparent #e0aa7e;
}

.text {
  width: 100%;
  text-align: justify;
  margin: 0;
  color: #2b1000;
  font-size: 1.3rem;
}

.task:last-child {
  margin-bottom: 0;
}

.buttons {
  position: absolute;
  right: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.btn {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  margin: 2px 0;
}

.edit-btn {
  background-color: transparent;
  border: none;
}

.edit-btn:hover {
  background-color: #f2ca7a;
}

.remove-btn {
  background-color: transparent;
  border: none;
}

.remove-btn:hover {
  background-color: #ee8f75;
}

.save-btn {
  background-color: transparent;
  border: none;
}

.save-btn:hover {
  background-color: #c3f7ab;
}

.task__modification-field {
  border: 2px solid #e2ac74;
  font-size: 1.5rem;
}

textarea {
  display: block;
  width: 100%;
  -webkit-task-sizing: border-box;
     box-sizing: border-box;
  border-radius: 5px;
  padding: 10px;
  resize: none;
}

